import React, { ReactElement } from "react"
import { Helmet } from "react-helmet"
import Header from "../components/header"
import SideNav from "../components/sideNav"
import useSiteMetadata from "../hooks/siteMetadata"

function Layout({ children, title: pageTitle }: any): ReactElement {
  const { title } = useSiteMetadata()
  return (
    <>
      <Helmet>
        <title>{pageTitle || title}</title>
      </Helmet>
      <div className="container mx-auto">
        <Header />
        <div className="grid sm:grid-cols-none md:grid-cols-12 gap-6 p-4">
          <div className="col-span-3 md:col-span-4 lg:col-span-3 hidden md:block">
            <SideNav />
          </div>
          <div className="col-span-9 md:col-span-8 lg:col-span-9">
            {children}
          </div>
        </div>
      </div>
    </>
  )
}

export default Layout
